.menu li {
    display: inline-block;
    padding: 5px 20px;
    font-size: 20px;
    margin: 2px;     
}
.menu li a {
    text-decoration: none;
    color: midnightblue;
    -webkit-transition: 0.65s ease;
    -o-transition: 0.65s ease;
    transition: 0.65s ease
}
.menu li a:hover {
    color: firebrick;
}
.menu {
    background-color: darkgrey;
    padding-top: 20px;
    padding-bottom: 20px;
    margin: 0;
    text-align: left;  
    position: relative; 
    -webkit-transition: 10.5s ease-in-out; 
    -o-transition: 10.5s ease-in-out; 
    transition: 10.5s ease-in-out; 
}
.menu li .active{
    color: firebrick;
}




